import React, {useMemo} from 'react';
import {useAppSelector} from '../../store/hooks';
import {useWindowDimensions, View} from 'react-native';
import {Text} from 'react-native-paper';

export function EditorRuler() {
  const editor = useAppSelector(state => state.editor);
  const {pixelsPerSecond} = editor;
  const {width} = useWindowDimensions();
  const increments = useMemo(() => {
    const seconds = width / pixelsPerSecond;
    const inc: number[] = [];
    while (inc.length < seconds) {
      const last = inc.length;
      inc.push(last + 1);
    }
    return inc;
  }, [width, pixelsPerSecond]);
  return (
    <View
      style={{
        width: '100%',
        height: 10,
        top: 0,
        left: 0,
        position: 'absolute',
        display: 'flex',
        flexDirection: 'row',
        gap: pixelsPerSecond - 1,
        zIndex: 1000,
      }}>
      {increments.map(increment => {
        return (
          <View
            style={{
              borderColor: 'white',
              borderLeftWidth: 1,
              height: increment % 2 ? 3 : 7,
            }}>
            {increment % 5 ? null : (
              <Text
                style={{
                  position: 'absolute',
                  left: pixelsPerSecond,
                  marginTop: 5,
                  fontSize: 10,
                }}>
                {increment}
              </Text>
            )}
          </View>
        );
      })}
    </View>
  );
}
